<template>
  <div class="tabs">
    <input type="radio" id="tab1" name="tab-control" checked>
    <input type="radio" id="tab2" name="tab-control">
    <MusicsPageHeader :currentPlaylistName="currentPlaylist" :isFavoriteTracksPageOpened="isFavorites" :isUploadsOpened="isUploads" :isPlaylistOpened="isPlaylist"/>   
    <div class="content">
      <Playlists :pageType="currentPageType" :isUploadsOpened="isUploads" :isPlaylistOpened="isPlaylist"/>
      <Favorites :pageType="currentPageType" />
    </div>
  </div>
</template>
<script>
import {mapGetters} from 'vuex'
import Playlists from './Playlists'
import MusicsPageHeader from './MusicsPageHeader'
import Favorites from './Favorites'
import universalParse from "id3-parser/lib/universal"

// Exporting data for current template
export default {
  data() {
    return {
      isUploads: false,
      isPlaylist: false,
      isFavorites: false,
      currentPageType: -1,
      currentPlaylist: 'My Playlists'
    }
  },
  components: {
    MusicsPageHeader,
    Playlists,
    Favorites
  },
  mounted() {
    window.addEventListener("resize", this.checkScreenWidth)
    this.$root.$emit('deactLoadingRoot')
  },
  beforeMount() {
    this.$root.$emit('actLoadingRoot')
    this.checkScreenWidth()
    this.$root.$on("openInstance", this.openInstance)
  },
  beforeDestroy() {
    this.$root.$off("addSongToPlaylist", this.addSongToPlaylist)
    this.$root.$off("openInstance", this.openInstance)
    window.removeEventListener("resize", this.checkScreenWidth)
  },
  methods: {
    checkScreenWidth() {
      this.$root.$emit('checkScreenWidth')
    },
    openInstance(type, name) {
      switch (type) {
        // Uploads page
        case 0: {
          this.isUploads = true
          this.isFavorites = false
          this.isPlaylist = false
          this.currentPageType = 0
          this.currentPlaylist = name
        }
        break
        // Playlist page
        case 1: {
          this.isUploads = false
          this.isFavorites = false
          this.isPlaylist = true
          this.currentPageType = 1
          this.currentPlaylist = name
        }
        break
        // Favorites page
        case 2: {
          this.currentPageType = 2
          this.isFavorites = true
        }
        break
        // Exit playlists
        case 3: {
          this.isUploads = false
          this.isFavorites = false
          this.isPlaylist = false
          this.currentPageType = -1
          this.currentPlaylist = 'My Playlists'
        }
        // Back to playlists || uploads
        default:
        {
          this.currentPageType = this.currentPlaylist === 'My Uploads' ? 0 : 1
          this.isFavorites = false
        }
          break
      }
    }
  }
}
</script>
<style>
.tabs {
  left: 50%;
  /* -webkit-transform: translateX(-50%); */
  /* transform: translateX(-50%); */
  /* position: relative; */
  background: white;
  padding-top: 20px;
  padding-bottom: 80px;
  width: 90%;
  height: auto;
  min-width: 240px;
  margin-left: auto;
  margin-right: auto;
}
.tabs input[name="tab-control"] {
  display: none;
}
.tabs .content section h2,
.tabs ul li label {
  font-family: "Niramit", sans-serif;
  font-weight: bold;
  font-size: 18px;
  color: #f39d93;
}
.tabs ul {
  list-style-type: none;
  padding-left: 0;
  display: flex;
  flex-direction: row;
  margin-bottom: 10px;
  justify-content: space-between;
  align-items: flex-end;
  flex-wrap: wrap;
  margin-top: 20px;
}
.tabs ul li {
  box-sizing: border-box;
  flex: 1;
  width: 50%;
  padding: 0 10px;
  text-align: center;
}
.tabs ul li label {
  transition: all 0.3s ease-in-out;
  color: #929daf;
  padding: 5px auto;
  overflow: hidden;
  text-overflow: ellipsis;
  display: block;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  white-space: nowrap;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.tabs ul li label br {
  display: none;
}
.tabs ul li label svg {
  fill: #929daf;
  height: 1.2em;
  vertical-align: middle;
  margin-right: 0.2em;
  transition: all 0.2s ease-in-out;
}
.tabs ul li label:hover,
.tabs ul li label:focus,
.tabs ul li label:active {
  outline: 0;
  color: #bec5cf;
}
.tabs ul li label:hover svg,
.tabs ul li label:focus svg,
.tabs ul li label:active svg {
  fill: #bec5cf;
}
.tabs .slider {
  position: relative;
  width: 50%;
  transition: all 0.33s cubic-bezier(0.38, 0.8, 0.32, 1.07);
}
.tabs .slider .indicator {
  position: relative;
  width: 80%;
  margin: 0 auto;
  height: 4px;
  background: #f39d93;
  border-radius: 1px;
}
.tabs .content {
  margin-top: 30px;
}
.tabs .content section {
  display: none;
  -webkit-animation-name: content;
  animation-name: content;
  -webkit-animation-direction: normal;
  animation-direction: normal;
  -webkit-animation-duration: 0.3s;
  animation-duration: 0.3s;
  -webkit-animation-timing-function: ease-in-out;
  animation-timing-function: ease-in-out;
  -webkit-animation-iteration-count: 1;
  animation-iteration-count: 1;
  line-height: 1.4;
}
.tabs .content section h2 {
  color: #f39d93;
  display: none;
}
.tabs .content section h2::after {
  content: "";
  position: relative;
  display: block;
  width: 30px;
  height: 3px;
  background: #f39d93;
  margin-top: 5px;
  left: 1px;
}
.tabs
  input[name="tab-control"]:nth-of-type(1):checked
  ~ div
  ul
  > li:nth-child(1)
  > label {
  cursor: default;
  color: #f39d93;
}
.tabs
  input[name="tab-control"]:nth-of-type(1):checked
  ~ div
  ul
  > li:nth-child(1)
  > label
  svg {
  fill: #f39d93;
}
@media (max-width: 600px) {
  .tabs
    input[name="tab-control"]:nth-of-type(1):checked
    ~ div
    ul
    > li:nth-child(1)
    > label {
    background: rgba(0, 0, 0, 0.08);
  }
}
.tabs input[name="tab-control"]:nth-of-type(1):checked ~ div .slider {
  -webkit-transform: translateX(0%);
  transform: translateX(0%);
}
.tabs
  input[name="tab-control"]:nth-of-type(1):checked
  ~ .content
  > section:nth-child(1) {
  display: block;
}
.tabs
  input[name="tab-control"]:nth-of-type(2):checked
  ~ div
  ul
  > li:nth-child(2)
  > label {
  cursor: default;
  color: #f39d93;
}
.tabs
  input[name="tab-control"]:nth-of-type(2):checked
  ~ div
  ul
  > li:nth-child(2)
  > label
  svg {
  fill: #f39d93;
}
@media (max-width: 600px) {
  .tabs
    input[name="tab-control"]:nth-of-type(2):checked
    ~ div
    ul
    > li:nth-child(2)
    > label {
    background: rgba(0, 0, 0, 0.08);
  }
}
.tabs input[name="tab-control"]:nth-of-type(2):checked ~ div .slider {
  -webkit-transform: translateX(100%);
  transform: translateX(100%);
}
.tabs
  input[name="tab-control"]:nth-of-type(2):checked
  ~ .content
  > section:nth-child(2) {
  display: block;
}
.tabs
  input[name="tab-control"]:nth-of-type(3):checked
  ~ div
  ul
  > li:nth-child(3)
  > label {
  cursor: default;
  color: #f39d93;
}
.tabs
  input[name="tab-control"]:nth-of-type(3):checked
  ~ div
  ul
  > li:nth-child(3)
  > label
  svg {
  fill: #f39d93;
}
@media (max-width: 600px) {
  .tabs
    input[name="tab-control"]:nth-of-type(3):checked
    ~ div
    ul
    > li:nth-child(3)
    > label {
    background: rgba(0, 0, 0, 0.08);
  }
}
.tabs input[name="tab-control"]:nth-of-type(3):checked ~ div .slider {
  -webkit-transform: translateX(200%);
  transform: translateX(200%);
}
.tabs
  input[name="tab-control"]:nth-of-type(3):checked
  ~ .content
  > section:nth-child(3) {
  display: block;
}
.tabs
  input[name="tab-control"]:nth-of-type(4):checked
  ~ div
  ul
  > li:nth-child(4)
  > label {
  cursor: default;
  color: #f39d93;
}
.tabs
  input[name="tab-control"]:nth-of-type(4):checked
  ~ div
  ul
  > li:nth-child(4)
  > label
  svg {
  fill: #f39d93;
}
@media (max-width: 600px) {
  .tabs
    input[name="tab-control"]:nth-of-type(4):checked
    ~ div
    ul
    > li:nth-child(4)
    > label {
    background: rgba(0, 0, 0, 0.08);
  }
}
.tabs input[name="tab-control"]:nth-of-type(4):checked ~ div .slider {
  -webkit-transform: translateX(300%);
  transform: translateX(300%);
}
.tabs
  input[name="tab-control"]:nth-of-type(4):checked
  ~ .content
  > section:nth-child(4) {
  display: block;
}
@-webkit-keyframes content {
  from {
    opacity: 0;
    -webkit-transform: translateY(5%);
    transform: translateY(5%);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0%);
    transform: translateY(0%);
  }
}
@keyframes content {
  from {
    opacity: 0;
    -webkit-transform: translateY(5%);
    transform: translateY(5%);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0%);
    transform: translateY(0%);
  }
}
@media (max-width: 1000px) {
  .tabs ul li label {
    white-space: initial;
  }
  .tabs ul li label br {
    display: initial;
  }
  .tabs ul li label svg {
    height: 1.5em;
  }
}
@media (max-width: 600px) {
  .tabs ul li label {
    padding: 5px;
    border-radius: 5px;
  }
  .tabs ul li label span {
    display: none;
  }
  .tabs .slider {
    display: none;
  }
  .tabs .content {
    margin-top: 20px;
  }
  .tabs .content section h2 {
    display: block;
  }
}
</style>



